function cardfliprender(img) {
	var degree = arguments.length <= 1 || arguments[1] === undefined ? 0 : arguments[1];
	var x = arguments.length <= 2 || arguments[2] === undefined ? 0 : arguments[2];
	var y = arguments.length <= 3 || arguments[3] === undefined ? 0 : arguments[3];
	var scale = arguments.length <= 4 || arguments[4] === undefined ? 1 : arguments[4];
	var rotation = arguments.length <= 5 || arguments[5] === undefined ? 0 : arguments[5];

	var toRad = Math.PI / 180;
	var rad = degree * toRad;
	var rotateRad = rotation * toRad;

	var width = img.width;
	var height = img.height;
	var halfHeight = height / 2;

	var widthToHeightRatio = height / width;
	var skewSin = .2 * Math.sin(rad) * scale;
	var xScale = Math.cos(rad) * scale;
	var yScale = (1 - skewSin / (widthToHeightRatio * 2)) * scale;

	var heightOffset = skewSin * height / (widthToHeightRatio * 4);
	var widthOffset = width / 2 - width * xScale / 2;

	for (var i = 0; i <= halfHeight; ++i) {
		var drawHeightTop = halfHeight - i;
		var yskew = skewSin * i / height;
		// Top half
		ctx.setTransform(xScale, -yskew, 0, yScale, widthOffset + x, heightOffset + y);
		utils.drawImage(ctx, img, 0, drawHeightTop, width, 2, 0, drawHeightTop, width, 2);

		// Bottom Half
		var drawHeightBottom = halfHeight + i;
		ctx.setTransform(xScale, yskew, 0, yScale, widthOffset + x, heightOffset + y);
		utils.drawImage(ctx, img, 0, drawHeightBottom, width, 2, 0, drawHeightBottom, width, 2);
	}
}
